
   
    /* Here we import our mark's font (Righteous) */
        @import url('https://fonts.googleapis.com/css2?family=Creepster&family=Righteous&display=swap');

    .container_contacts {
        background-color: #006080;
        font-family: 'Righteous', cursive;
        max-width: 100vw;
        height: 37vh;
        border-radius: 2%;
        margin: -255px 0px -25px 0px;
        /* z-index: 1; */
    }

    .social_medias {
        display: flex;  align-items: center; align-content: center; 
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        list-style: none;
        max-width: 100vw;
        padding: 16px 0px 10px 0px;
    }

    .social_medias li {
        margin: 0px 11px 0px 0px;
        padding-left: 25px;
    }

    .container_contacts {
        display: inline-block;
        width: 100%;
    }

    .container_contacts h2 {
        text-align: center;
        color: #fff;
        padding-top: 50px;
        margin-bottom: 20px;
    }

    .container_contacts h3 {
        text-align: center;
        color: #fff;
        padding-top: 70px;
    }